@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,700,900);
body{margin:0;font-family:"Poppins", sans-serif !important;-webkit-font-smoothing:antialiased !important;-moz-osx-font-smoothing:grayscale !important;font-size:0.9375rem !important;height:100%}body h1,body h2,body h3,body h4,body h5,body h6,body p,body a{margin:0;font-family:"Poppins", sans-serif !important;-webkit-font-smoothing:antialiased !important;-moz-osx-font-smoothing:grayscale !important;font-size:0.9375rem !important;height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}button.anchor{background:none !important;border:none;padding:0 !important;color:#069;text-decoration:underline;cursor:pointer}button.anchor.footer{color:white}

.login.container{background-image:linear-gradient(to top, rgba(0,0,0,0.65), rgba(0,0,0,0.55)),url("https://user-images.githubusercontent.com/43678736/120118513-8b1abc00-c158-11eb-9f3c-32f3dad78d93.jpg");background-repeat:no-repeat;background-size:cover;background-position:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:100vh;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.login.container .form{border-radius:4px;padding:5% 3%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;width:400px;background-color:rgba(255,255,255,0.75)}.login.container .form img{border-radius:4px;width:87%;margin-bottom:5%}.login.container .form a{cursor:pointer;-webkit-align-self:flex-end;align-self:flex-end;margin-top:3%}@media screen and (max-width: 600px){.login.container .form{width:80%}.login.container .form img{width:65%;margin-bottom:5%}}

.main-wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-content:space-between;align-content:space-between;-webkit-justify-content:stretch;justify-content:stretch;min-height:100vh;background-color:#f8f9fa}.sub-header-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;background-color:#f8f9fa}.body{border-radius:4px;margin:2%;min-height:calc(100vh - 288px);background-color:#ffffff;width:auto}@media screen and (max-width: 600px){.body{height:calc(100vh - 170px)}}

.footer-container{background-color:#15558d;color:white;padding:1%;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}.footer-container a{color:white}.footer-container .section{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.footer-container .section img{-webkit-align-self:flex-start;align-self:flex-start}

.avatar{border-radius:50%;height:55px;cursor:pointer}@media screen and (max-width: 600px){.avatar{height:50px}}.logo{margin:2;height:40px;cursor:pointer}

.top-header-container{background-color:#15558d;color:white;padding:1% 3%}.top-header-container h2{margin:0%}

